  --primary-color: #eedabd;
  --secondary-color: #bca37f;
  --liner-gradient: linear-gradient(
    71.9deg,
    #354c6282 10.77%,
    #8633332b 92.46%
  );
  --muted-color: #46484b;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-size: 12px;
  color: #424242;
  min-height: 100vh;
}

.skip-content {
  left: 0;
  left: 0;
  top: -100px;
  z-index: 100;
  padding: 10px;
  color: white;
  font-size: 20px;
  position: absolute;
  text-decoration: none;
  background-color: red;
}
.skip-content:focus {
  top: 200px;
}

.nav-mobile {
  width: 100%;
  padding: 8px;
  display: flex;
  color: white;
  background-color: #bca37f;
  justify-content: space-between;
}

.icon-menu {
  margin-left: 5px;
  font-size: 34px;
  text-decoration: none;
  color: white;
  width: 70px;
  text-align: center;
  display: flex;
}

.icon-menu:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.wrap {
  width: 100%;
  padding: 10px;
  background-color: #bca37f;
  height: 70px;
  display: grid;
  grid-template-columns: 44px 1fr;
  align-content: center;
}

.logo {
  display: flex;
  color: white;
  font-size: 20px;
  font-weight: 500;
  min-height: 50px;
  margin-left: 15px;
  align-items: center;
  text-decoration: none;
  justify-content: center;
}

.nav-list-mobile {
  width: 300px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-content: center;
}

.nav-items-mobile {
  margin-right: 30px;
  text-decoration: none;
  color: white;
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60px;
}

.nav-items-mobile:hover {
  cursor: pointer;
  transform: scale(1.15);
}

.hero {
  width: 100%;
  display: flex;
  min-height: 380px;
  text-align: center;
  align-items: center;
  background-position: center;
  background-color: black !important;
  background-image: linear-gradient(rgba(0, 0, 5, 0.3),rgba(0, 0, 5, 0.1)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.hero-inner {
  margin: 0 auto;
  max-width: 800px;
}

.hero-title {
  color: #fff;
  font-size: 36px;
  font-weight: 600;
}

.hero-subtitle {
  color: #fff;
  margin-top: 10px;
  font-size: 18px;
  font-weight: 600;
}

main {
  width: 100%;
  margin: 0 auto;
}

.content {
  padding: 32px;
}

.latest {
  width: 100%;
  margin: 30px 0;
  text-align: center;
}

.label {
  font-size: 2em;
  color: #000;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.label:hover {
  color: #bca37f;
}

.list {
  margin: 32px auto auto;
  text-align: left;
  display: grid;
  grid-row-gap: 16px;
}

.list_item {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
}

.list_item_thumbnail {
  width: 100%;
  max-height: 200px;
  transition: transform 0.3s ease;
}

.list_item_thumbnail:hover {
  transform: scale(1.03);
}

.city {
  top: 10px;
  right: 10px;
  color: white;
  font-size: 14px;
  min-height: 45px;
  font-weight: 500;
  position: absolute;
  border-radius: 20px;
  padding: 12px 20px 5px;
  transition: all 0.3s ease;
  background-color: #bca37f;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
}

.city:hover {
  background-color: #765827;
  transform: scale(1.1);
}

.list_item_content {
  padding: 16px;
}

.list_item_rating {
  font-size: 16px;
  margin-top: 18px;
  margin-bottom: -10px;
  color: #000;
  min-height: 44px;
  font-weight: 500;
}

.list_item_rating_value {
  color: #fff;
  min-width: 60px;
  font-size: 16px;
  font-weight: 500;
  min-height: 60px;
  padding: 18px 18px;
  border-radius: 50px;
  text-decoration: none;
  background-color: #bca37f;
  transition: background-color 0.3s ease;
}

.list_item_rating_value:hover {
  background-color: #765827;
}

.list_item_title {
  color: #000;
  min-height: 50px;
  margin-top: -2px;
  transition: transform 0.3s ease;
}

.list_item_title:hover {
  color: #bca37f;
  transform: scale(1.05);
}

.list_item_title a {
  padding: 10px;
  color: inherit;
  font-size: 20px;
  min-width: 70px;
  min-height: 70px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list_item_description {
  color: #000;
  margin-top: 20px;
  font-size: 12px;
  line-height: 1.5em;
}

.footer-text {
  width: 100%;
  color: #fff;
  padding: 15px;
  font-size: 16px;
  line-height: 50px;
  text-align: center;
  background-color: #bca37f;
  margin-top: auto;
}

.footer-text p {
  min-width: 50px;
  min-height: 50px;
}

.container {
  padding: 0 10px;
}

.explore-restaurant-list__not__found {
  text-align: center;
  width: 100%;
}
