___CSS_LOADER_EXPORT___.push([module.id, `#detail-rest {
  padding-top: 10px;
}

.detail {
  display: grid;
  grid-gap: 1.545em;
  font-size: 1em;
  margin-top: 1em;
}

.img-container {
  height: 300px;
  border-radius: 0.5em;
  margin-bottom: 10px;
  position: relative;
}

.detail-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 0.5em;
}

.img-container span {
  background: var(--secondary-color);
  color: #ffffff;
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: x-large;
  border-radius: 1em;
  padding: 0.4em 1.3em;
}

.detail-info li {
  position: relative;
  display: block;
  padding: 5px 0;

  text-decoration: none;
}

.detail-name-address-rating {
  display: inline-block;
}

.resto-name {
  font-size: 2.6em;
  text-align: left;
}

.resto-rating {
  font-size: 2em;
  text-align: left;
  margin-bottom: 20px;
}

.resto-rating span {
  border-radius: 1em;
  padding: 0.4em 1.3em;
  background-color: var(--secondary-color);
  color: white;
}

.detail h3 {
  font-size: 2em;
  font-weight: bold;
}

.detail-desc {
  text-align: justify;
  font-size: 1.2em;
  color: var(--muted-color);
}

.resto-category {
  margin-top: 12px;
}

.category {
  padding: 0.3em 1em;
  margin-right: 2px;
  border: 1px solid transparent;
  border-radius: 10px;
  text-align: left;
  background-color: var(--primary-color);
  font-size: large;
}

.detail-menu {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 30px;
}

.detail-menu h4 {
  font-weight: bold;
  font-size: 1.5em;
  padding: 1em;

  margin: 0;
}

.detail-food {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
}

.detail-food ul,
.detail-drink ul {
  padding-left: 0;
}

.detail-food li {
  padding: 0.5em 0;
  display: block;
  text-decoration: none;
  background-color: var(--primary-color);
  border: 0.5px solid var(--secondary-color);
  border-width: 0 0 0.5px;
  font-size: 1.37em;
}

.detail-drink {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
}

.detail-drink li {
  padding: 0.5em 0;
  display: block;
  text-decoration: none;
  background-color: var(--primary-color);
  border: 0.5px solid var(--secondary-color);
  border-width: 0 0 0.5px;
  font-size: 1.37em;
}

.detail-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-left: 0;
  margin-bottom: 0;
}

.detail-info h4 {
  font-size: 1.5em;
  margin: 0;
  text-align: left;
  padding-left: 0.5em;
}

.resto-address {
  font-size: 1.4em;
  margin: 0;
  text-align: left;
}

/* DETAIL REVIEW */
.detail-review {
  max-width: 100%;
  font-size: 1em;
  text-align: center;
}

.detail-review-item {
  background-color: var(--primary-color);
  margin-bottom: 1.5em;
  padding: 1em 1em;
  border: 1px solid var(--secondary-color);
  border-radius: 2em;
}

.detail-review-item:last-child {
  margin-bottom: 0;
}

.header-review {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em;

  border-bottom: 1px solid var(--secondary-color);
}

.name-review {
  font-weight: bold;
  display: flex;
  font-size: 1.5em;
  margin: 0;
}

.date-review {
  font-size: 1em;
  font-weight: lighter;
  font-size: 1em;
}

.body-review {
  padding: 1em;
  text-align: left;
  font-size: 1.37em;
}

.category:hover,
.detail-food li:hover,
.detail-drink li:hover,
.detail-review-item:hover {
  box-shadow: 0px 10px 13px -7px var(--secondary-color),
    5px 5px 15px 5px var(--secondary-color);
}

form {
  margin: 2em 0;
  padding: 1.5em;
  border: 3px solid var(--secondary-color);
  border-radius: 15px;
}

.formctrl {
  display: block;
  width: 100%;
  padding: 0.738em;
  font-size: 1.37em;

  background-color: var(--primary-color);
  border: 1px solid var(--secondary-color);
  border-radius: 1em;
}

.formlab {
  display: block;
  font-size: 1.3em;
  margin-bottom: 0.5em;
  text-align: left;
}

.bg-3 {
  margin-bottom: 1.3em;
  margin-top: 0.5em;
}

.formbtn {
  display: block;
  width: 100px;
  padding: 0.7em;
  font-size: 1.37em;
  color: var(--primary-color);
  background-color: #007bff;
  border: 2px solid #007bff;
  border-radius: 1em;
}

.formbtn:hover {
  box-shadow: 0px 10px 13px -7px var(--secondary-color),
    5px 5px 15px 5px var(--secondary-color);
}

.like {
  font-size: 18px;
  background-color: var(--secondary-color);
  border: 1px solid white;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 16px;
  right: 16px;
  z-index: 10;
}

.heart-like {
  color: rgb(221, 58, 58);
}

.heart-unlike {
  color: white;
}

.error {
  color: #ffffff;
  font-size: 1.5em;
  margin: 0;
  text-align: center;
}

.detail-flex {
  width: 100%;
  gap: 30px;
  margin-bottom: 40px;
}

.detail-flex .flex-menu {
  flex-grow: 1;
  width: 100%;
}

.detail-container {
  padding: 0 30px;
}
