  width: 100%;
  display: flex;
  justify-content: center;
  margin: 10px;
}

.spinner {
  display: inline-block;
}
.spinner:after {
  content: ' ';
  display: block;
  width: 100px;
  height: 100px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid rgba(235, 10, 10, 0.2);
  border-color: rgba(235, 10, 10, 0.2) transparent rgba(235, 10, 10, 0.2)
    transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
